import axios from 'axios';
import React from 'react';
import '../../stylesheets/Book.css';
import { MDBCard, MDBCardImage, MDBCardBody, MDBCardTitle, MDBCardText, MDBCardLink, MDBListGroup, MDBListGroupItem } from 'mdb-react-ui-kit';

export default class BookList extends React.Component
{

    state={
        books: [],
        error: []
    }

    componentDidMount()
    {
        
        axios.get("https://raw.githubusercontent.com/rahmanreza646/k-book-list-json/main/db.json")

        .then(response => response.data)
        .then( data => {

            this.setState({
                books: data
            })
        })

        .catch(error=>{
            this.setState({
                error:error
            })
        } )
        

    }
    render()
    {

        const allbooks=this.state.books.map((book,index)=>
        {
            return(
                <MDBCard  className='card' >
      <MDBCardImage position='top' alt='...' src={book.img} />
      <MDBCardBody>
        <MDBCardTitle>{book.name}</MDBCardTitle>
        <MDBCardText>
          Some quick example text to build on the card title and make up the bulk of the card's content.
        </MDBCardText>
      </MDBCardBody>
      <MDBListGroup flush>
        <MDBListGroupItem> Writer: {book.writer}</MDBListGroupItem>
        <MDBListGroupItem> Translated By: {book.translated_by}</MDBListGroupItem>
        
      </MDBListGroup>
      <MDBCardBody>
        <MDBCardLink href='#'>Details</MDBCardLink>
        
      </MDBCardBody>
    </MDBCard>
               
            )

        })


        return(
            <div>
                {allbooks}
            </div>
        )

    }


}

